import {
  Typography, Card, CardContent, Button, makeStyles, ButtonGroup,
  Select, MenuItem, Grid, Paper, FormControlLabel, Switch, Theme, createStyles
} from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { Title, useLocale, useSetLocale, useTranslate, useGetIdentity } from 'react-admin'
import { useDispatch, useSelector } from 'react-redux'
import { List, Search } from '@material-ui/icons'
import { ThemeState } from '../../core/common/theme.reducer'
import { CHANGE_THEME } from '../../core/constants/reducer.actions'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    label: { width: '15vw', display: 'inline-block' },
    button: { margin: theme.spacing(2) },
    select: { width: '40vw', margin: theme.spacing(2) },
    compsContainer: { width: '40vw', display: 'inline-block', margin: theme.spacing(2) }
  }))

const DashboardCustomization = () => {
  const { identity } = useGetIdentity()
  const [componentsOpen, setComponentsOpen] = useState({
    ticketfinder: localStorage.getItem(`${identity?.id}.ticketfinder`) === '1',
    dashlogs: localStorage.getItem(`${identity?.id}.dashlogs`) === '1',
  })
  const translate = useTranslate()

  const componentsChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setComponentsOpen({
      ...componentsOpen,
      [event.target.name]: event.target.checked,
    })
    localStorage.setItem(`${identity?.id}.${event.target.name}`, event.target.checked ? '1' : '0')
  }

  useEffect(() => {
    setComponentsOpen({
      ticketfinder: localStorage.getItem(`${identity?.id}.ticketfinder`) === '1',
      dashlogs: localStorage.getItem(`${identity?.id}.dashlogs`) === '1',
    })
  }, [identity])

  return (
    <Grid container spacing={1}>
      <Grid item xs={6}>
        <Paper>
          <Search />
          <FormControlLabel
            control={
              <Switch
                checked={componentsOpen.ticketfinder}
                name='ticketfinder'
                onChange={componentsChange} />}
            label={translate('dashboard.settings.ticket-finder-label')}
            labelPlacement="top"
            style={{ margin: '5%', textAlign: 'center', width: '90%' }}
          />
        </Paper>
      </Grid>
      <Grid item xs={6}>
        <Paper>
          <List />
          <FormControlLabel
            control={
              <Switch
                checked={componentsOpen.dashlogs}
                name='dashlogs'
                onChange={componentsChange} />}
            label={translate('dashboard.settings.last-logs-label')}
            labelPlacement="top"
            style={{ margin: '5%', textAlign: 'center', width: '90%' }}
          />
        </Paper>
      </Grid>
    </Grid>
  )
}

export const Configuration = () => {
  const dispatch = useDispatch()
  const theme = useSelector((state: ThemeState) => state.theme.darkMode === 'dark')
  const classes = useStyles()
  const locale = useLocale()
  const setLocale = useSetLocale()
  const translate = useTranslate()

  return (
    <Card>
      <Title title={translate("settings.name")} />
      <CardContent>
        <Typography className={classes.label}>{translate("settings.darkMode.name")}</Typography>
        <ButtonGroup className={classes.button}>
          <Button
            size="large"
            variant="contained"
            color={!theme ? 'primary' : 'default'}
            onClick={() => theme && dispatch({ type: CHANGE_THEME })}>
            {translate("settings.darkMode.light")}
          </Button>
          <Button
            size="large"
            variant="contained"
            color={theme ? 'primary' : 'default'}
            onClick={() => !theme && dispatch({ type: CHANGE_THEME })}>
            {translate("settings.darkMode.dark")}
          </Button>
        </ButtonGroup>
      </CardContent>
      <CardContent>
        <Typography className={classes.label}>{translate("settings.language.name")}</Typography>
        <Select
          value={locale}
          onChange={e => setLocale(e.target.value as string)}
          className={classes.select}
          variant="outlined">
          <MenuItem value="pl">{translate("settings.language.pl")}</MenuItem>
          <MenuItem value="en">{translate("settings.language.en")}</MenuItem>
        </Select>
      </CardContent>
      <CardContent>
        <Typography className={classes.label}>{translate("dashboard.settings.title")}</Typography>
        <div className={classes.compsContainer}>
          <DashboardCustomization />
        </div>
      </CardContent>
    </Card>
  )
}