import { DataProvider, GetListResult } from "react-admin"
import { EVENTS_URL } from '../../api-urls'
import { getWithPagination } from '../../common/fetch.utils'
import { ALL_RESULTS } from "../../common/filter.values"
import { NameFilter } from "../../common/name.filter"
import { EventDto } from "../../dto/event.dto"
import { EventOverallView } from "./event-overall-view"
import { mapSortOverallViewParam } from "./overall-view-list-sort.mapper"
import { mapToEventOverallView } from "./overall-view-list.mapper"

const provider = {
  getList: async (
    resource: string,
    { filter, sort: { order, field }, pagination: { page, perPage } }: NameFilter,
  ): Promise<GetListResult<EventOverallView>> => {
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortOverallViewParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `${filter.name ? `e=>e.Name.Contains("${filter.name}")` : ALL_RESULTS}?${pathParams}`

    const { data, range: { total } } = await getWithPagination<EventDto[]>(
      `${EVENTS_URL}/GetByConditions/`, path)

    const overallViewList = data.map((event) => mapToEventOverallView(event))

    return {
      data: overallViewList,
      total
    }
  },
} as DataProvider

export default provider