import { MenuItem, Collapse, Tooltip, ListItemIcon, Typography, List } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { ExpandMore } from '@material-ui/icons';
import React, { ReactElement, ReactNode } from 'react';
import { useSelector } from 'react-redux';
import { ReduxState } from 'react-admin';

const useStyles = makeStyles(theme => ({
  icon: {
    minWidth: theme.spacing(5)
  },
  sidebarIsOpen: {
    '& a': {
      transition: 'padding-left 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms',
      paddingLeft: theme.spacing(4),
    },
  },
  sidebarIsClosed: {
    '& a': {
      transition: 'padding-left 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms',
      paddingLeft: theme.spacing(2),
    },
  },
}));

interface SubMenuProps {
  handleToggle: () => void
  icon: ReactElement
  isOpen: boolean
  name: string
  children: ReactNode
}

export const SubMenu = (props: SubMenuProps) => {
  const { handleToggle, icon, isOpen, name, children } = props
  const sidebarOpen = useSelector((state: ReduxState) => state?.admin.ui.sidebarOpen)
  const classes = useStyles()

  const header = (
    <MenuItem button onClick={handleToggle}>
      <ListItemIcon className={classes.icon}>
        {isOpen ? <ExpandMore /> : icon}
      </ListItemIcon>
      <Typography variant="inherit" color="textSecondary">
        {name}
      </Typography>
    </MenuItem>
  )

  return (
    <>
      {sidebarOpen || isOpen ? (
        header
      ) : (
        <Tooltip title={name} placement="right">
          {header}
        </Tooltip>
      )}
      <Collapse in={isOpen} timeout="auto" unmountOnExit>
        <List
          component="div"
          disablePadding
          className={sidebarOpen ? classes.sidebarIsOpen : classes.sidebarIsClosed}>
          {children}
        </List>
      </Collapse>
    </>
  )
}
