import { makeStyles } from '@material-ui/core/styles';
import { ViewComfy, Receipt, List, Sort } from '@material-ui/icons';
import React, { useState } from 'react';
import { DashboardMenuItem, MenuItemLink, useTranslate, ReduxState } from 'react-admin';
import { useSelector } from 'react-redux';
import { SubMenu } from './AppSubmenu';

const useStyles = makeStyles({
  open: {
    width: "240px",
  },
  closed: {
    width: "55px"
  },
});

export const AppMenu = () => {
  const sidebarOpen = useSelector((state: ReduxState) => state.admin.ui.sidebarOpen)
  const theme = useStyles()
  const [open, setOpen] = useState({
    menuStats: true
  })
  const translate = useTranslate()

  const handleToggle = (menu: "menuStats") => setOpen(state => ({ ...state, [menu]: !state[menu] }))

  return (
    <div
      style={{ userSelect: "none" }}
      className={sidebarOpen ? theme.open : theme.closed}>
      <DashboardMenuItem />
      <MenuItemLink
        to="/ticket-info"
        primaryText={translate('resources.ticket-info.name')}
        leftIcon={<Receipt />} />
      <MenuItemLink
        to="/logs"
        primaryText={translate('resources.logs.name')}
        leftIcon={<List />} />
      <SubMenu
        handleToggle={() => handleToggle("menuStats")}
        isOpen={open.menuStats}
        name={translate('side-menu.stats.name')}
        icon={<ViewComfy />}>
        <MenuItemLink
          to="/stats-overall-view-list"
          primaryText={translate('side-menu.stats.overall-view')}
          leftIcon={<Sort style={{ transform: "scaleX(-1)" }} />} />
      </SubMenu>
    </div>
  )
}