import { Card, CardContent, CardHeader, createStyles, Grid, makeStyles, Theme } from '@material-ui/core'
import React, { useEffect } from 'react'
import { ResourceContextProvider, useGetIdentity, useTranslate } from 'react-admin'
import { ResourceName } from '../core/ResourceName'
import { FindUserByTicket } from './common/FindUserByTicket'
import { LogList } from './logs/LogList'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    header: {
      textAlign: 'center',
      letterSpacing: theme.spacing(0.2),
    },
  }),
)

const TicketFinder = () => (
  <Card style={{ width: '100%', marginBottom: '5px' }}>
    <FindUserByTicket />
  </Card>
)

const LastLogs = () => {
  const t = useTranslate()
  return (
    <Card style={{ width: '100%', marginBottom: '5px' }}>
      <CardHeader title={t('dashboard.cards.last-logs.title')} />
      <CardContent>
        <ResourceContextProvider value={ResourceName.LOGS}>
          <LogList
            basePath="/"
            title=" "
            compactMode
            pagination={false}
            perPage={10}
            actions={false}
            filters={undefined}
          />
        </ResourceContextProvider>
      </CardContent>
    </Card>
  )
}

export const Dashboard = () => {
  const { identity } = useGetIdentity()
  const classes = useStyles()
  const t = useTranslate()

  useEffect(() => {
    ;['dashlogs', 'ticketfinder'].map((component) => {
      if (!localStorage.getItem(`${identity?.id}.${component}`))
        localStorage.setItem(`${identity?.id}.${component}`, '1')
      return component
    })
  }, [identity])

  return (
    <Grid container spacing={1}>
      <Grid item xs={12}>
        <Card>
          <CardHeader
            className={classes.header}
            title={t('dashboard.welcome')}
            titleTypographyProps={{ variant: 'h4' }}
          />
        </Card>
      </Grid>
      <Grid item sm={6} xs={12}>
        {localStorage.getItem(`${identity?.id}.ticketfinder`) === '1' && (
          <TicketFinder />
        )}
      </Grid>
      <Grid
        item
        sm={6}
        xs={12}
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'flex-end',
          width: '10%',
        }}
      >
        {localStorage.getItem(`${identity?.id}.dashlogs`) === '1' && (
          <LastLogs />
        )}
      </Grid>
    </Grid>
  )
}